/**
 * # LocalStorage helpers
 * > Some utilities about local storage
 * make sure local storage is available before anything else.
 *
 * @author Kevin Bastien <kevin@kffein.com>
 */


export default class LocalStorageHelper {
  /**
   * format a date within dayjsjs
   * @returns {String} formated date
   * @param {String} date
   * @param {String} format - like yyyy/MM/dd
   *
   * @exemple: `return LocalStorageHelper.formatDate(this.start, this.$t('FORMATS.DATE.HOURS_MINUTES'));`
   */
  static storageAvailable() {
    let storage;
    try {
      storage = window['localStorage'];
      const x = "__storage_test__";
      storage.setItem(x, x);
      storage.removeItem(x);
      return true;
    } catch (e) {
      return (
        e instanceof DOMException &&
        // everything except Firefox
        (e.code === 22 ||
          // Firefox
          e.code === 1014 ||
          // test name field too, because code might not be present
          // everything except Firefox
          e.name === "QuotaExceededError" ||
          // Firefox
          e.name === "NS_ERROR_DOM_QUOTA_REACHED") &&
        // acknowledge QuotaExceededError only if there's something already stored
        storage &&
        storage.length !== 0
      );
    }
  }

  static get(key: string) {
    if (this.storageAvailable()) {
      // Yippee! We can use localStorage awesomeness
      return localStorage.getItem(key);
    }
    // Too bad, no localStorage for us
    return null;
  }
  static set(key: string, val: any) {
    if (this.storageAvailable()) {
      // Yippee! We can use localStorage awesomeness
      return localStorage.setItem(key, val);
    }
    // Too bad, no localStorage for us
    return null
  }
  static remove(key: string) {
    if (this.storageAvailable()) {
      // Yippee! We can use localStorage awesomeness
      return localStorage.removeItem(key);
    }
    // Too bad, no localStorage for us
    return null
  }
}
